import { Plugin, Modal,  ItemView, WorkspaceLeaf, App, PluginSettingTab, Setting, TFile, MarkdownRenderer, MarkdownPostProcessorContext } from 'obsidian';
import { myCommand } from './commands';
import { MyPluginSettingTab } from './settings';

interface MyPluginSettings {
  mySetting: string;
}

const DEFAULT_SETTINGS: MyPluginSettings = {
  mySetting: 'default',
};

export default class MyPlugin extends Plugin {
  settings: MyPluginSettingTab;

  async onload() {
    await this.loadSettings();
    this.addCommand(myCommand);
  }

  async loadSettings() {
    this.settings = Object.assign({}, DEFAULT_SETTINGS, await this.loadData());
  }

  async saveSettings() {
    await this.saveData(this.settings);
  }
}